let mobileClass = mobileAndTabletcheck() ? 'mobile' : 'desktop';

document.documentElement.classList.add(mobileClass);

// alert boxes =====================
function showHide(el) {
	el.classList.toggle('hidden');
}

let overlayEl = document.querySelector('.overlays');

// make all close buttons work
let allAlerts = overlayEl.children;
allAlerts = Array.from(allAlerts);
// console.log(allAlerts);
allAlerts.forEach((el) => {
	let closeBut = el.lastElementChild;
	// console.log(closeBut);
	closeBut.addEventListener('click', () => {
		// all close buttons should hide overlays container

		showHide(overlayEl);

		// if a close button is on a temp message hide the message as well

		if (el.classList.contains('message')) {
			showHide(el);
		}
	})
})


// if a close button is on info just hide container (above)
// make sure info is shown when info icon is clicked (there can be more per page, but the info icon in the nav displays the first one)
let allInfos = document.querySelectorAll('.info');
let infoIcon = document.querySelector('#infoIcon');

if (infoIcon) {
	infoIcon.addEventListener('click', () => {
		// show/hide overlay
		showHide(overlayEl);
		// if info has hidden class remove it (will if a message has been shown)
		if (allInfos[0].classList.contains('hidden')) {
			showHide(allInfos[0])
		}
	});
}
